'use client';

import React from 'react';
import Image from 'next/image';
import { useTheme } from 'next-themes';

interface CertificateCardProps {
  title: string;
  subtitle: string;
  status: 'locked' | 'completed';
  image: string;
  lockedImage: string;
  descriptionLocked: string;
  descriptionUnlocked: string;
  shareUrl: string;
}

const CertificateCard: React.FC<CertificateCardProps> = ({
  title,
  subtitle,
  status,
  image,
  lockedImage,
  descriptionLocked,
  descriptionUnlocked,
  shareUrl,
}) => {
  const { theme } = useTheme();
  const isCompleted = status === 'completed';

  const lockIcon = `/assets/${theme === 'dark' ? 'dark' : 'light'}/${isCompleted ? 'unlocked.svg' : 'lock.svg'}`;

  const socialLinks = [
    {
      name: 'WhatsApp',
      url: `https://api.whatsapp.com/send?text=Mira mi certificado: ${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/whatsapp.svg`,
    },
    {
      name: 'Facebook',
      url: `https://www.facebook.com/sharer/sharer.php?u=${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/facebook.svg`,
    },
    {
      name: 'LinkedIn',
      url: `https://www.linkedin.com/sharing/share-offsite/?url=${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/linkedin.svg`,
    },
  ];

  return (
    <div
      className={`relative p-6 rounded-xl border shadow-md flex flex-col justify-between text-left min-h-[500px] bg-[var(--background-1)] ${
        isCompleted ? 'opacity-100' : 'opacity-50'
      }`}
    >
      {/* Icono estado */}
      <div className="absolute top-6 right-6">
        <Image src={lockIcon} alt="Estado" width={20} height={20} />
      </div>

      {/* Título y subtítulo */}
      <div>
        <h2 className="p16 font-bold text-[var(--color-primary-900)]">{title}</h2>
        <p className="p12 text-[var(--color-neutral-600)]">{subtitle}</p>
      </div>

      {/* Mensaje desbloqueado o bloqueado */}
      <div className="mt-4 space-y-2">
        {isCompleted && (
          <p className="p16 font-bold text-[var(--color-primary-900)]">
            ¡Felicidades por completar el Nivel {title}!
          </p>
        )}
        <p className="p12 text-[var(--color-primary-700)]">
          {isCompleted ? descriptionUnlocked : descriptionLocked}
        </p>
      </div>

      {/* Imagen */}
      <div className="mt-4 flex justify-center">
        <Image
          src={isCompleted ? image : lockedImage}
          alt={title}
          width={220}
          height={160}
          className="rounded-md object-contain"
        />
      </div>

      {/* Botón descarga */}
      {isCompleted && (
        <button className="mt-6 px-6 py-2 border border-[var(--primary-color)] text-[var(--primary-color)] text-sm font-bold rounded-lg hover:bg-[var(--primary-color)] hover:text-white transition">
          Descargar Certificado
        </button>
      )}

      {/* Compartir */}
      {isCompleted && (
        <div className="mt-4">
          <p className="p12 font-bold text-[var(--color-neutral-600)] mb-2">Compártelo</p>
          <div className="flex gap-4 justify-center">
            {socialLinks.map(({ name, url, icon }) => (
              <a key={name} href={url} target="_blank" rel="noopener noreferrer">
                <Image src={icon} alt={name} width={20} height={20} />
              </a>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default CertificateCard;
